<template>
  <div class="index-backg">
    <!-- 網站導引 -->
    <header class="header-wrapper">
      <AppHeader :cartItemCount="cartQuantity" @OpenSidebar="toggleSidebar" />
    </header>
    <!-- 狀態提示 -->
    <div v-if="isShowToast" class="toast">
      <p>{{ toastMessage }}</p>
    </div>
    <!-- 右側側欄 -->
    <div class="sidebar" :class="{ active: isSidebarOpen }">
      <!-- 側欄標題與關閉按鈕 -->
      <div class="sidebar-header">
        <span class="sidebar-title">購物車</span>
        <button class="close-btn" @click="toggleSidebar">×</button>
      </div>
      <!-- 購物車內容 -->
      <div class="sidebar-content">
        <CartPage :cart="cart" @update-cart="updateCart" @close-sidebar="toggleSidebar(false)" />
      </div>
    </div>
    <!-- Breadcrumbs區塊 -->
    <!-- 
    <div class="Breadcrumbs">
      <Breadcrumbs />
    </div> -->
    <!-- 主要內容區塊 -->
    <section class="main-cotent">
      <router-view :cart="cart" @add-to-cart="addToCart" @update-cart="updateCart" />
    </section>
    <!-- Footer區塊 -->
    <footer class="footer">
      <p>本網站僅供個人作品使用，未提供商業用途</p>
      <p>BBS &copy; 2025 COPYRIGHT</p>
    </footer>
  </div>
</template>

<script>
import { ref,computed, onMounted, provide } from 'vue';
// 引入 vuex 的 useStore
import { useStore } from 'vuex';
// 引入其他元件
import AppHeader from './components/AppHeader.vue';
import CartPage from './views/CartPage.vue';
// 引入共享資料
import { newsData } from './newsData.js';
import { menuData } from './menuData.js';

export default {
  components: { AppHeader, CartPage },
  setup() {
    const store = useStore();
    const isSidebarOpen = ref(false);
    const isShowToast = ref(false);
    const toastMessage = ref('');

    // 加入購物車並存入localStorage
    const addToCart = (item) => {
      store.commit('addToCart', item);
      localStorage.setItem('cart', JSON.stringify(store.state.cart));
      toastMessage.value = `${item.name} 已成功加入購物車！`;
      isShowToast.value = true;
      // 2秒後自動隱藏提示
      setTimeout(() => {
        isShowToast.value = false;
      }, 2000);
    };

    // 載入時讀取 localStorage 並更新 Vuex 資料
    onMounted(() => {
      const savedCart = localStorage.getItem('cart');
      if (savedCart) {
        store.commit('setCart', JSON.parse(savedCart));
      }
    });

    // 使用 computed 來計算 cart 中商品的總數量
    const cartQuantity = computed(() => store.getters.cartQuantity);

    // 控制側欄開關
    const toggleSidebar = () => {
      isSidebarOpen.value = !isSidebarOpen.value; 
    };

    // 提供 newsData 和 menuData 給子元件
    provide('newsData', ref(newsData));
    provide('menuData', ref(menuData));

    return { 
      cart: store.state.cart, 
      addToCart, 
      toggleSidebar, 
      isSidebarOpen, 
      cartQuantity, 
      isShowToast, 
      toastMessage 
    };
  }
};
</script>